const io = require('socket.io-client');
let socketAddress;
const EventEmitterCreator = require('events')
const EventEmitter = new EventEmitterCreator();
let server_cords;
let startTime;
let pause;
let stop;
let left;
let right;
let soundState = true; let soundTrackLink;

var canvas = document.getElementById('canvas');
var ctx = canvas.getContext('2d');
canvas.width = 650;
canvas.height = 400;
var score1 = 0;
var score2 = 0;
var keys = {};
let accelerometerGamma;
window.addEventListener('keydown', function (e) {
    keys[e.keyCode] = true;
    // e.preventDefault();
});
window.addEventListener('deviceorientation', ev => {
    if (ev.gamma) {
        accelerometerGamma = ev.gamma;
    }
}, true);
window.addEventListener('keyup', function (e) {
    delete keys[e.keyCode];
});
window.addEventListener('keydown', (e) => {
    if (e.keyCode == 80) {
        if (!stop) {
            EventEmitter.emit('pause');
        }
    }
    if (e.keyCode == 13) {
        sendMessage();
    }
})

function Box(options) {
    this.x = options.x || 10;
    this.y = options.y || 10;
    this.width = options.width || 40;
    this.height = options.height || 50;
    this.color = options.color || '#FFFFFF';
    this.speed = options.speed || 2;
    this.gravity = options.gravity || 2;
}

var player1 = new Box({
    x: 10,
    y: 200,
    width: 15,
    height: 80,
    color: '#FFFFFF',
    gravity: 2
});
var player2 = new Box({
    x: 625,
    y: 100,
    width: 15,
    height: 80,
    color: '#FFFFFF',
    gravity: 2
});
var midLine = new Box({
    x: (canvas.width / 2) - 2.5,
    y: -1,
    width: 5,
    height: canvas.height + 1,
    color: '#FFFFFF'
});
var theBall = new Box({
    x: (canvas.width / 2),
    y: (canvas.height / 2),
    width: 15,
    height: 15,
    color: '#FFFFFF',
    speed: 1,
    gravity: 1
})

function input() {
    if (server_cords) {
        if (right) {
            player1 = server_cords.player1;
        }
        if (left) {
            player2 = server_cords.player2;
        }
    }
    let player;
    if (left) {
        player = player1;
    } else if (right) {
        player = player2;
    }
    if (accelerometerGamma && window.mobileAndTabletCheck()) {
        const futurePlayerPos = ((canvas.height / 2) - (player.height / 2)) + accelerometerGamma * 5;
        if (futurePlayerPos > 0) {
            if (futurePlayerPos + player.height < canvas.height) {
                player.y = futurePlayerPos;
            }
        }
    } else {
        if (38 in keys) {
            if (player.y - player.gravity > 0) {
                player.y -= player.gravity;
            }
        } else if (40 in keys) {
            if (player.y + player.height + player.gravity < canvas.height) {
                player.y += player.gravity;
            }
        }
    }
}

function drawBox(box) {
    ctx.fillStyle = box.color;
    ctx.fillRect(box.x, box.y, box.width, box.height);
}

function displayScore1() {
    ctx.font = "20px Arial";
    ctx.fillStyle = "rgb(255,255,255)";
    let str;
    // if (server_cords) { str = server_cords.score1 } else { str = score1 };
    str = score1;
    ctx.fillText(str, (canvas.width / 2) - 50, 30);
}

function displayScore2() {
    ctx.font = "20px Arial";
    ctx.fillStyle = "rgb(255,255,255)";
    let str;
    // if (server_cords) { str = server_cords.score2 } else { str = score2 };
    str = score2;
    ctx.fillText(str, (canvas.width / 2) + 50, 30);
}

function displayInfo(info) {
    ctx.font = "24px Arial";
    ctx.fillStyle = "rgb(255,255,255)";
    let str = info;
    ctx.fillText(str, (canvas.width / 2) - 160, (canvas.height / 2));
}

function displayNumbers(info) {
    ctx.font = "34px Arial";
    ctx.fillStyle = "rgb(255,255,255)";
    let str = info;
    ctx.fillText(str, (canvas.width / 2) - 10, (canvas.height / 2));
}

function ballBounce() {
    if (server_cords) {
        theBall = server_cords.theBall;
    }
    if (((theBall.y + theBall.gravity) <= 0) || ((theBall.y + theBall.gravity + theBall.height) >= canvas.height)) {
        theBall.gravity = theBall.gravity * -1;
        theBall.y += theBall.gravity;
        theBall.x += theBall.speed;
    } else {
        theBall.x += theBall.speed;
        theBall.y += theBall.gravity
    }
    ballCollision();
}

function playBeeps() {
    if (Math.round(Math.random() * 1) == 0) {
        document.getElementsByTagName('audio')[0].play().then(() => {});
    } else {
        document.getElementsByTagName('audio')[1].play().then(() => {});
    }
}

function ballCollision() {
    if (((theBall.x + theBall.speed <= player1.x + player1.width) && (theBall.y + theBall.gravity > player1.y) && (theBall.y + theBall.gravity <= player1.y + player1.height))
        || ((theBall.x + theBall.width + theBall.speed >= player2.x) && (theBall.y + theBall.gravity > player2.y) && (theBall.y + theBall.gravity <= player2.y + player2.height))) {
        playBeeps();
    } else if (theBall.x + theBall.speed < player1.x) {
        playBeeps();
    } else if (theBall.x + theBall.speed > player2.x + player2.width) {
        playBeeps();
    }
    draw();
}

function draw() {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    displayScore1();
    displayScore2();
    drawBox(player1);
    drawBox(player2);
    drawBox(midLine);
    drawBox(theBall);
}

function loop() {
    if (!pause && !stop) {
        ballBounce();
        input();
        if (left) {
            EventEmitter.emit('sendCords', {player1: player1});
        } else if (right) {
            EventEmitter.emit('sendCords', {player2: player2});
        }
        window.requestAnimationFrame(loop);
    } else {
        if (pause) {
            document.getElementById('ping').innerText = `Game paused by one of the players (Press 'P' to continue)`;
        }
    }
}

document.getElementById('chatSendButton').onclick = sendMessage;

function sendMessage() {
    if (document.getElementById('chatInput').value) {
        if (document.getElementById('chatInput').value.includes('connect ')) {
            socketAddress = document.getElementById('chatInput').value.substr(8);
            gameStart();
        }
        if (left) {
            EventEmitter.emit('sendMessage', `player 1 says: ${document.getElementById('chatInput').value}`);
        } else if (right) {
            EventEmitter.emit('sendMessage', `player 2 says: ${document.getElementById('chatInput').value}`);
        }
        document.getElementById('chatInput').value = '';
    }
}

function gameStart() {
    if (window.mobileAndTabletCheck()) {
        socketAddress = 'https://pong-game-host-diploma.herokuapp.com/';
    }
    if (socketAddress) {
        connect(socketAddress);
    } else {
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        displayInfo('connect [ipV4]');
    }
    EventEmitter.on('connected', () => {
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        displayInfo('Waiting other player to connect');
        EventEmitter.on('start', () => {
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            displayNumbers('3');
            setTimeout(() => {
                ctx.clearRect(0, 0, canvas.width, canvas.height);
                displayNumbers('2');
                setTimeout(() => {
                    ctx.clearRect(0, 0, canvas.width, canvas.height);
                    displayNumbers('1');
                    setTimeout(() => {
                        ctx.clearRect(0, 0, canvas.width, canvas.height);
                        displayInfo('start');
                        stop = false;
                        document.getElementsByTagName('audio')[2].play().then(() => { setTimeout(() => {playSoundtrack()}, 350) });
                        loop();
                    }, 1000);
                }, 1000);
            }, 1000);
        });
    });
}

function clearChat() {
    document.getElementById('chat').innerHTML = '';
}

function definePlayerSideVisualisation() {
    if (left && !mobileAndTabletCheck()) {
        let e = document.getElementById('canvas-wrapper').getElementsByTagName('p');
        for(i = 0; i < e.length; i++) { if (i == 0) { e[i].style.display = 'block'; } else { e[i].style.display = 'none'; } }
    } else if (left && mobileAndTabletCheck()) {
        let e = document.getElementById('canvas-wrapper').getElementsByTagName('p');
        for(i = 0; i < e.length; i++) { if (i == 1) { e[i].style.display = 'block'; } else { e[i].style.display = 'none'; } }
    } else if (right && !mobileAndTabletCheck()) {
        let e = document.getElementById('canvas-wrapper').getElementsByTagName('p');
        for(i = 0; i < e.length; i++) { if (i == 2) { e[i].style.display = 'block'; } else { e[i].style.display = 'none'; } }
    } else if (right && mobileAndTabletCheck()) {
        let e = document.getElementById('canvas-wrapper').getElementsByTagName('p');
        for(i = 0; i < e.length; i++) { if (i == 3) { e[i].style.display = 'block'; } else { e[i].style.display = 'none'; } }
    }
}

function clearPlayerSideVisualisation() {
    let e = document.getElementById('canvas-wrapper').getElementsByTagName('p');
    for(i = 0; i < e.length; i++) { e[i].style.display = 'none'; }
}

function playSoundtrack() {
    if (soundState) {
        document.getElementById('canvas-wrapper').getElementsByTagName('img')[0].style.display = 'block';
        document.getElementsByTagName('audio')[3].volume = 0.15;
        document.getElementsByTagName('audio')[3].play().then(() => { playSoundtrack(); });
    } else {
        document.getElementById('canvas-wrapper').getElementsByTagName('img')[1].style.display = 'block';
    }
}

document.getElementById('canvas-wrapper').getElementsByTagName('img')[0].onclick = changeSoundState;
document.getElementById('canvas-wrapper').getElementsByTagName('img')[1].onclick = changeSoundState;
function changeSoundState(clear) {
    soundState = !soundState;
    if (!soundState) {
        let audio = document.getElementsByTagName('audio')[3];
        audio.pause(); audio.currentTime = 0;
        document.getElementById('canvas-wrapper').getElementsByTagName('img')[0].style.display = 'none';
        document.getElementById('canvas-wrapper').getElementsByTagName('img')[1].style.display = 'block';
    } else {
        playSoundtrack();
        document.getElementById('canvas-wrapper').getElementsByTagName('img')[0].style.display = 'block';
        document.getElementById('canvas-wrapper').getElementsByTagName('img')[1].style.display = 'none';
    }
    if (clear) {
        soundState = false;
        let audio = document.getElementsByTagName('audio')[3];
        audio.pause(); audio.currentTime = 0;
        buttons = document.getElementById('canvas-wrapper').getElementsByTagName('img')
        buttons[0].style.display = 'none';
        buttons[1].style.display = 'none';
    }
}

function connect(address) {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    displayInfo(`Connecting to ${address}`);
    socket = io.connect(address);

    socket.on('connect', () => {
        socket.on('player_left_game', (res) => {
            stop = true;
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            displayInfo('Sorry but other player left the game!');
            socket.disconnect();
            socketAddress = undefined;
            clearChat(); changeSoundState(true); clearPlayerSideVisualisation();
            setTimeout(() => {
                gameStart();
            }, 3000);
        })

        socket.on('connected', (res) => {
            EventEmitter.emit('connected');
        })

        socket.on('full_server', () => {
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            displayInfo('Sorry but server is full, try again later!');
            socket.disconnect();
            clearChat(); changeSoundState(true);
            setTimeout(() => {
                gameStart();
            }, 3000);
        });

        socket.on('server_cords', (res) => {
            server_cords = res;
        })

        socket.on('pong', (res) => {
            latency = Date.now() - startTime;
            document.getElementById('ping').innerText = `ping: ${latency}`;
        })

        socket.on('score', (res) => {
            score1 = res.score1;
            score2 = res.score2;
        })

        socket.on('start_position', (res) => {
            if (res == 'left') {
                left = true;
            } else if (res == 'right') {
                right = true;
            }
            definePlayerSideVisualisation();
        })

        socket.on('start', () => {
            EventEmitter.emit('start');
        })

        socket.on('pause', () => {
            if (!stop) {
                pause = true;
            }
        })

        socket.on('continue', () => {
            if (!stop) {
                pause = false;
                loop();
            }
        })

        socket.on('chat_update', (res) => {
            let message = document.createElement('span');
            message.innerText = res;
            document.getElementById('chat').appendChild(message);
        })

        socket.on('end_game', (res) => {
            stop = true;
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            displayInfo(res);
            socket.disconnect();
            socketAddress = undefined;
            clearChat(); changeSoundState(true); clearPlayerSideVisualisation();
            setTimeout(() => {
                gameStart();
            }, 3000);
        })

        EventEmitter.on('sendCords', (data) => {
            startTime = Date.now();
            socket.emit('cords', data);
        })

        EventEmitter.on('pause', () => {
            if (pause) {
                pause = false;
                loop();
                socket.emit('continue');
            } else {
                pause = true;
                socket.emit('pause');
            }
        })

        EventEmitter.on('add_score', (data) => {
            socket.emit('add_score', data);
        })

        EventEmitter.on('sendMessage', (data) => {
            socket.emit('chat_message', data);
        })
    })
}

gameStart();
